interface Board {
    blocks: Array<number>,
}

interface Move {
    type: MoveType,
    submoves?: Array<Move>,
}

enum MoveType {
    Move,
    Hold,
    AddLine,
}

enum Direction {
    Left,
    Right,

    Down,
    Drop,

    RotateRight,
    RotateLeft,
    Rotate180,
}